


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f0f8ff; 
  color: #333;
  text-align: center;
  padding: 20px;
}

h1 {
  color: #ff6347; 
  font-size: 3em;
  margin-bottom: 20px;
}


#clickMeButton {
  background-color: #ff6347; 
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1.2em;
  cursor: pointer;
  border-radius: 8px;
}

#clickMeButton:hover {
  background-color: #ff4500; 
}
